// This option must meet the default theme color

@import "_variables";

/**
 * File Name: style-switcher.css
 */

.style-switcher {
  // display: none !important;
  direction: ltr !important;
  width: 320px;
  height: 100%;
  text-align: center;
  background: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  font-family: "Open Sans", sans-serif;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 21;
    	  transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transform: translateX(100%);
      	  transform: translateX(100%);

  .btn.purchase-btn {
    font-size: 16px;
  	background: #82b440;
  	border-color: #82b440;
    box-shadow: none;
  }

  .btn.dark,
  .btn.hover-dark:hover {
    background: #111;
    border-color: #111;
    box-shadow: none;
  }

  /* Scroll Settings */
  .ss-wrapper {
    width: calc(100% + 18px);
    padding-bottom: 25px;
  }

  .ss-scroll {
  	border-radius: 0;
  	background: rgba(0, 0, 0, 0.2);
  }
}

.style-switcher.show {
  -webkit-transform: translateX(0);
    	  transform: translateX(0);
}

.style-switcher ul {
  list-style: outside none;
}

/* Style Switcher Icon */
.ss-icon {
  color: #fff;
  background: #222;
  font-size: 25px;
  width: 55px;
  height: 55px;
  line-height: 50px;
  text-align: center;
  position: absolute;
  top: 211px;
  left: -55px;
  border-radius: 10px 0 0 10px;
  box-shadow: -2px 0 7px rgba(0, 0, 0, 0.2);
}

.ss-icon:hover, .ss-icon:focus {
  color: #fff;
}

.ss-icon.rotating * {
    -webkit-animation: ssIconRotate 5s linear infinite;
      -ms-animation: ssIconRotate 5s linear infinite;
        animation: ssIconRotate 5s linear infinite;
}

@-webkit-keyframes ssIconRotate {
    from{
        -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    }
}

@-ms-keyframes ssIconRotate {
    from{
        -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    }
}

@keyframes ssIconRotate {
    from{
        -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    }
}


/* Style Switcher Title */
.style-switcher h3 {
    height: 75px;
    line-height: 75px;
    padding: 0 20px;
    margin: 0;
    font-size: 18px;
    text-align: center;
    word-spacing: 0px;
    font-weight: 600;
    background: #f9f9f9;
    letter-spacing: 0;
}


/* Style Switcher Content */
.style-switcher-content {
  background: #fff;
  // box-shadow: 0 0 15px 10px rgba(0, 0, 0, 0.03);
  /*box-shadow: -3px 0 50px -2px rgba(0, 0, 0, 0.14);*/
  padding: 30px 25px;
  overflow-y: auto;
  height: calc(100% - 50px);
}

.style-switcher-content .ss-intro {
  margin-top: 20px;
}

.style-switcher-content p {
  font-size: 12px;
  line-height: 18px;
}

/* Style Switcher Content Box */
.ss-content-box {
  & + .ss-content-box {
  	margin-top: 30px;
  	padding-top: 30px;
  	border-top: 1px solid #eee;
  }

  > h5 {
    margin-bottom: 15px;
    font-size: 15px;
  }
}

.ss-content-box:last-child {
  margin-bottom: 0;
}

/* Colors Skins */
.colors-skins {
  font-size: 0;
  margin-bottom: -5px; /* reversing "li" margin bottom */
}

.colors-skins li {
  margin: 0 5px 5px 0;
  display: inline-block;
  vertical-align: top;
}

.colors-skins li button {
  position: relative;
  display: block;
  width: 34px;
  height: 34px;
  padding: 0;
  border-radius: 9999px;
  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: none;
  background: #eee;
}

.colors-skins li button:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 9999px;
  opacity: 0;
  background: rgba(#fff, 0.3);
  transition: all 0.2s;
}

.colors-skins li button.active:after {
  width: 10px;
/*  content: "\f00c";
  font-family: "fontAwesome";
  font-size: 20px;
  color: #fff;
*/  height: 10px;
  top: 50%;
  left: 50%;
  opacity: 1;
  background: rgba(#fff, 0.55);
}

.colors-skins li button:hover:after {
  opacity: 1;
}

/* Default */
.colors-skins li button.default {
  background: $theme-color-1;
}

