/* Switch */
.switch {
  text-align: center;
  font-size: 0;

  & + .switch {
	  margin-top: 5px;
  }
}

.switch:last-child {
  margin-bottom: 0;
}

.switch span {
  font-size: 10px;
  line-height: 13px;
  position: relative;
  vertical-align: middle;
  text-transform: uppercase;
  display: inline-block;
  letter-spacing: 1px;
  width: 38.5%;
  transition: all 0.15s;
}

.switch span:first-child,
.switch span:last-child {
  transition: all 0s;
  cursor: pointer;
}

.switch span:first-child {
  padding-right: 8px;
  text-align: right;
}

.switch span:last-child {
  padding-left: 8px;
  text-align: left;
}

.switch span.active {
  color: #222;
  font-weight: 600;
}

/* Switch Button */
.switch-button {
  width: 45px !important;
  height: 24px;
  border: 1px solid #bbb;
  border-radius: 9999px;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.switch-button:after {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 18px;
  height: 18px;
  background: #222;
  border-radius: 9999px;
      transition: all 0.15s;
}

.switch-button.active:after {
  left: 23px;
  background: #f17663;
}

/* Demos Preview */
.demos-preview {
  font-size: 0;
}

.demos-preview li {
  vertical-align: top;
  // float: left;
  margin-bottom: 15px;
  // width: 50%;

  &:nth-child(odd) {
    // padding-right: 5px;
  }

  &:nth-child(even) {
    // padding-left: 5px;
  }

  & + li {
    // margin-top: 10px;
  }
}

.demos-preview li a {
  overflow: hidden;
  position: relative;
  display: block;
  max-height: 168px;
  width: calc(100% - 1px);
  border: 1px solid #e5e5e5;
  // padding: 5px;
  font-size: 10px;
  line-height: 13px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #f1f1f1;
  color: #888;

  &::after,
  h5 {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: all 0.2s;
  }

  &:hover {
    &::after,
    h5 {
      visibility: visible;
      opacity: 1;
    }

    h5 {
      transition-delay: 0.1s;
    }
  }

  &::after {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
  }

  h5 {
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 100%;
    padding: 0 20px;
    text-align: center;
    text-transform: capitalize;
    transform: translate(-50%, -50%);
    font-size: 16px;
    // font-weight: 500;
    color: $theme-color-1;
  }
}

.demos-preview li a:hover {
  color: #222;
  background: #e1e1e1;
}

/* Demos Preview Image */
.dp-img {
  position: absolute;
  left: calc(-100% - 10px);
  bottom: 150px;
  width: 100%;
  max-width: initial;
  border: 5px solid #fff;
  opacity: 0;
  visibility: hidden;
  transform: translateX(30px);
  transition: all 0.2s 0.3s;
  box-shadow: 0 0 15px rgba(0,0,0, 0.1);
  background: #ccc;

  &.appeared {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
  transition: all 0.2s;
  }
}


