/* Simple Scroll */
.ss-wrapper {
  // overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  float: left;

  // Override
  margin-left: -18px;
  width: calc(100% + 0); // Space for scroller related with margin-left.
  overflow-x: hidden;
}

.ss-wrapper > .ss-content {
  height: 100%;
  width: 100%;
  padding: 0 32px 0 0;
  position: relative;
  right: -18px; // -18px
  overflow: auto;
  box-sizing: border-box;

  // Override
  padding: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

.ss-scroll {
  position: absolute;
  background-color: rgba(0,0,0,0.1);
  width: 9px;
  border-radius: 4px;
  top: 0;
  z-index: 2;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.25s linear;

  // Override
  right: 0 !important;
  // opacity: 0.5;
  width: 6px;
  transition: color 0.25s, opacity 0.25s;
}

.ss-hidden {
  display: none;
}

.ss-container:hover .ss-scroll {
  opacity: 1;
}

.ss-grabbed {
  -o-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

